import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			scoreList:[],
			loaded:false,
			loadingType:'loading',
			page:1,
			day:0.00,
			month:0.00,
			year:0.00
			
		}
	},
	onLoad() {
		this.loadData();
	},
	onPullDownRefresh() {
		let self=this;
		setTimeout(function(){
			self.page=1;
			self.loadData();
			uni.stopPullDownRefresh();
		},800);
	},
	methods: {
			//异步请求  收藏列表的数据
		async loadData() {
			let self = this;
			//请求我的分销统计
			self.$axios(self.$baseUrl.getMyScore,{})
			.then(res=>{
				console.log(res);
				if(res.data.code == 1){
					self.day=res.data.data.today;
					self.month=res.data.data.tomonth;
					self.year=res.data.data.toyear;
				}else{
					self.$api.msg(res.data.msg);
				}
			},(error) =>{
				console.log(error);
			})
			
			//请求我的分销记录
			self.$axios(self.$baseUrl.getScoreLog,
			{page:self.page})
			.then(res =>{
				console.log(res);
				if(res.data.code == 1){
					let serverList=res.data.data.data;
					if(self.page==1){
						self.scoreList=[];
					}
					for (let index in serverList) {
						console.log(serverList[index]);
						self.scoreList.push(serverList[index]);
					}
					self.loaded=true;
					if(serverList.length<10){
						self.$data.loadingType='nomore'
					}else{
						self.$data.loadingType='loading'
					}
					
				}else{
					self.$api.msg(res.data.msg);
				}
				
			},(error) =>{
				console.log(error)
			})
		
		},
		loadMore(){
			//如果没有更多了  不需要调接口了
			if(this.loadingType==='nomore')return;
			let self=this;
			setTimeout(function(){
				self.page+=1;
				self.loadData();
			},800);
			
		}
	}
}
